<template>
  <div class="ArticleCard-warp">
    <h5 class="mt-0 mb-1">{{title}}</h5>

    <b-media left-align vertical-align="center">
      <b-img
        slot="aside"
        :src="iconImg"
        height="80"
        width="80"
        blank
        blank-color="#ccc"
        alt="placeholder"
      />
      <p class="mb-0">{{content}}</p>
    </b-media>
    <b-media>
      <b-div>
        <b-img
          slot="aside"
          :src="authorIcon"
          blank
          blank-color="#ccc"
          width="20"
          alt="placeholder"
        />
        {{author}}
      </b-div>
      <b-div>
        <b-img
          slot="aside"
          :src="defaultWatchIcon"
          blank
          blank-color="#ccc"
          width="20"
          alt="placeholder"
        />
        {{watch}}
      </b-div>
      <b-div>
        <b-img
          slot="aside"
          :src="defaulCollectIcon"
          blank
          blank-color="#ccc"
          width="20"
          alt="placeholder"
        />
        {{collect}}
      </b-div>
      <b-div>
        <b-img
          slot="aside"
          :src="defaulCommentIcon"
          blank
          blank-color="#ccc"
          width="20"
          alt="placeholder"
        />
        {{comment}}
      </b-div>

      <b-div class="foot-b-media-right">
        <!-- time -->
        <b-div>
          <b-img
            slot="aside"
            :src="defaulPublishTimeIcon"
            blank
            blank-color="#ccc"
            width="20"
            alt="placeholder"
          />
          {{publishTime}}
        </b-div>
        <b-div v-for="(item,index) in lable" :key="index">
          <!-- article-lable -->
          <b-article-lable class="article-lable">{{item}}</b-article-lable>
        </b-div>
        <b-div>
          <!-- article-from -->
          <b-article-lable class="article-from">{{from}}</b-article-lable>
        </b-div>
      </b-div>
    </b-media>
  </div>
</template>

<script>
export default {
  props: {
    id: String,
    title: String,
    content: String, //正文
    iconImg: String, //图片展示
    publishTime: String, //发表时间
    authorIcon: String, //作者头像
    author: String, //作者
    watch: String, //阅读量
    comment: String, //评论
    collect: String, //收藏
    from: String, //原创、转载
    lable: [] //文章标签：VUE JAVA SpringBoot ...
  },

  data() {
    return {
      defaultWatchIcon: "",
      defaulCollectIcon: "",
      defaulCommentIcon: "",
      defaulPublishTimeIcon: ""
    };
  }
};
</script>

<style scoped>
.ArticleCard-warp {
  padding: 20px;
}
</style>


